import React, { Component } from "react";

export default function Item(props) {
    const {des} = props
    const {
        name
    } = des

    function showChild(item){
        var result = null

        if (item.header.length > 0) {
            result = item.header.map((_item, index) => {
                return (
                    <div class="form-group">
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" id={`gridCheck-${item.body[index]}`}  onChange = {(e) => this.onChange(e,item.body[index])} />
                            <label class="form-check-label" for={`gridCheck-${item.body[index]}`} >
                                {_item}
                            </label>
                        </div>

                    </div>
                );
            });
        }
        else {

        }
        return result
    }

  return (
    <div
      className="col-xs-3 col-sm-3 col-md-3 col-lg-3"
      style={{ marginBottom: "20px" }}
    >
      <h4
        style={{
          fontSize: "1.2rem",
          color: "rgb(16, 160, 181)",
          fontWeight: 500,
        }}
      >
        {name}
      </h4>
      {showChild(des)}

    </div>
  );
}
