import React, { Component } from "react";
import { Link } from "react-router-dom";
import permissionData from "./permission_data";
import Item from "./child/Item";
import history from "../../history";

export default function CreatPage() {
  return (
    <div className="container-fluid" style={{}}>
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h5 className="h4 title_content mb-0 text-gray-800">Sửa phân quyền</h5>
      </div>
      <div className="card shadow mb-4">
        <div className="card-body">
          <section className="content">
            <div className="row">
              <div className="col-md-12 col-xs-12">
                <div id="messages" />
                <div className="box">
                  <form role="form">
                    <div className="box-body">
                      <div className="form-group">
                        <label htmlFor="group_name">Tên phân quyền</label>
                        <input
                          type="text"
                          className="form-control"
                          id="group_name"
                          name="name"
                          placeholder="Nhập tên phân quyền"
                          autoComplete="off"
                          
                        />
                      </div>
                      <div className="form-group">
                        <label htmlFor="group_name">Mô tả phân quyền</label>
                        <input
                          type="text"
                          className="form-control"
                          id="id_group"
                          name="description"
                          placeholder="Nhập mô tả"
                          autoComplete="off"
                          
                        />
                      </div>
                      <div className="form-group">
                        <label
                          className="text-label-permission"
                          htmlFor="group_name"
                        >
                          Danh sách phân quyền
                        </label>
                        <div className="row">
                          {permissionData.map((item, index) => {
                            return <Item des={item}></Item>;
                          })}
                        </div>
                      </div>
                    </div>
                    <div className="box-footer">
                      <button type="submit" className="btn btn-info   btn-sm">
                        <i className="fas fa-save" /> Tạo
                      </button>
                      <button
                        type="button"
                        onClick={() => {
                          history.goBack();
                        }}
                        className="btn btn-warning   btn-sm"
                        style={{ marginLeft: "10px" }}
                      >
                        <i className="fas fa-arrow-left" /> Trở về
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}
